/* eslint-disable import/prefer-default-export */
import find from 'lodash/find';
import { DECLARATIVE_ACTION } from './actions';

export const geniusCardActionClicked = ({ action, dispatch, state }) => {
    const { payload } = action;
    const {
        properties: { actions, model },
    } = state;

    // actionId is passed in by now-button whereas
    // item is set by split button
    const { actionId, item } = payload;
    const assignmentId = actionId || item.id;
    const geniusCardAction = find(actions, ['assignmentId', assignmentId]);

    if (geniusCardAction)
        dispatch(DECLARATIVE_ACTION, { action: geniusCardAction, model });
};
