import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import { BUTTON_SLOT_STYLE, FOOTER_MORE_ACTION_LABEL } from '../constants';

const transformToSplitButtonItems = actions =>
    actions.map(({ assignmentId, label }) => ({
        id: assignmentId,
        label,
    }));

const groupActions = (actions, overflowLimit, allowDropdown) => {
    const isOverflow = allowDropdown && actions.length > overflowLimit;
    const displayActions = isOverflow
        ? actions.slice(0, overflowLimit - 1)
        : actions;
    const splitActions = isOverflow ? actions.slice(overflowLimit - 1) : []; // remaining actions

    return {
        displayActions,
        splitActions,
    };
};

const view = ({ actions, overflowLimit, allowDropdown }) => {
    const { displayActions, splitActions } = groupActions(
        actions,
        overflowLimit,
        allowDropdown
    );
    const splitButtonItems = transformToSplitButtonItems(splitActions);

    return (
        <div className="genius-card-footer">
            {displayActions.map(({ label, assignmentId }) => (
                <div className="genius-card-footer__control">
                    <now-button
                        append-to-payload={{
                            actionId: assignmentId,
                        }}
                        variant="secondary"
                        tooltipContent={label}
                    >
                        <div style={BUTTON_SLOT_STYLE}>
                            <span className="genius-card-footer__control_label">
                                {label}
                            </span>
                        </div>
                    </now-button>
                </div>
            ))}
            {splitButtonItems.length > 0 ? (
                <div className="genius-card-footer__control -dropdown">
                    <now-dropdown
                        items={splitButtonItems}
                        placeholder={FOOTER_MORE_ACTION_LABEL}
                        size="md"
                        variant="secondary"
                        select="none"
                    />
                </div>
            ) : null}
        </div>
    );
};

export default view;
