import '@servicenow/now-avatar';
import '@servicenow/now-image';
import '@servicenow/now-icon';
import isEmpty from 'lodash/isEmpty';

const MARKER_TYPES = {
    ICON: 'icon',
    IMAGE: 'image',
    AVATAR: 'avatar',
};

const getMarkerElem = ({ type, value, alt }) => {
    if (type === MARKER_TYPES.AVATAR) {
        return (
            <now-avatar
                imageSrc={value}
                size="lg"
                userName={alt}
                ariaHidden="false"
            />
        );
    }

    if (type === MARKER_TYPES.ICON) {
        return <now-icon icon={value} size="xl" ariaHidden="true" />;
    }

    if (type === MARKER_TYPES.IMAGE) {
        return <now-image src={value} width={74} alt={alt} />;
    }

    return null;
};

const isValid = type => Object.values(MARKER_TYPES).indexOf(type) !== -1;

const view = ({ marker }) => {
    if (isEmpty(marker)) {
        return null;
    }

    const { type } = marker;
    const validType = isValid(type);

    if (!validType) {
        return null;
    }

    const markerElem = getMarkerElem(marker);

    return (
        <div className={`genius-card__marker genius-card__marker--${type}`}>
            {markerElem}
        </div>
    );
};

export default view;
