import isEmpty from 'lodash/isEmpty';

import Description from '../contents/description';
import Marker from '../contents/marker';
import Header from '../contents/heading';
import Identifier from '../contents/identifier';
import LabelValues from '../contents/labelValues';

const view = ({
    identifier,
    identifierCriticalText,
    heading,
    marker,
    labelValuesInfo,
    description,
}) => (
    <div className="genius-card-body">
        {isEmpty(marker) ? null : (
            <div className="genius-card-body__column">
                <Marker marker={marker} />
            </div>
        )}
        <div className="genius-card-body__column -truncate">
            <Identifier
                idenitifier={identifier}
                identifierCriticalText={identifierCriticalText}
            />
            <Header heading={heading} />
            <Description description={description} />
            <LabelValues labelValuesInfo={labelValuesInfo} />
        </div>
    </div>
);

export default view;
