import CardHeader from './sections/heading';
import CardBody from './sections/body';
import CardFooter from './sections/footer';
import { MAX_DESKTOP_ACTIONS } from './constants';

const view = state => {
    const {
        properties: {
            actions,
            identifier,
            identifierCriticalText,
            heading,
            marker,
            labelValuesInfo,
            description,
        },
        behaviors: { mediaQuery },
    } = state;

    const isMobile = mediaQuery.mobileQuery;

    return (
        <div className="genius-card-container">
            <CardHeader />
            <CardBody
                identifier={identifier}
                identifierCriticalText={identifierCriticalText}
                heading={heading}
                marker={marker}
                labelValuesInfo={labelValuesInfo}
                description={description}
            />
            <CardFooter
                overflowLimit={MAX_DESKTOP_ACTIONS}
                actions={actions}
                allowDropdown={!isMobile}
            />
        </div>
    );
};

export default view;
