import isEmpty from 'lodash/isEmpty';

const view = ({ idenitifier, identifierCriticalText }) => {
    if (isEmpty(idenitifier) && isEmpty(identifierCriticalText)) return null;

    return (
        <div className="genius-card__text genius-card__identifier">
            <span className="genius-card__identifier-text">{idenitifier}</span>
            {identifierCriticalText ? (
                <span className="genius-card__identifier-text genius-card__identifier-text--critical">
                    {identifierCriticalText}
                </span>
            ) : null}
        </div>
    );
};

export default view;
