import has from 'lodash/has';
import isEmpty from 'lodash/isEmpty';
import isString from 'lodash/isString';
import unescape from 'lodash/unescape';
import { dangerouslyCreateElementFromString } from '@servicenow/ui-renderer-snabbdom';

const DESCRIPTION_MULTI_TYPES = {
    STACKED: 'stacked',
    HTML: 'html',
};

const getDescriptionStringElem = description => (
    <span className="genius-card__description-text">{description}</span>
);

const getDescriptionStackedElem = ({ values }) => (
    <div className="genius-card__description--stacked">
        {values.map(description => getDescriptionStringElem(description))}
    </div>
);

const getDescriptionHtmlElem = ({ values }) => {
    if (isEmpty(values)) return null;

    const [snippet] = values;

    if (isEmpty(snippet)) return null;

    return (
        <div className="genius-card__description-container">
            <div className="genius-card__description-wrapper">
                {dangerouslyCreateElementFromString(unescape(snippet))}
            </div>
        </div>
    );
};

const getDescriptionElem = description => {
    if (isString(description)) {
        return getDescriptionStringElem(description);
    }

    const { type } = description;

    const elementsDict = {
        [DESCRIPTION_MULTI_TYPES.HTML]: getDescriptionHtmlElem,
        [DESCRIPTION_MULTI_TYPES.STACKED]: getDescriptionStackedElem,
    };

    const resolvedType = has(elementsDict, type)
        ? type
        : DESCRIPTION_MULTI_TYPES.STACKED;

    return elementsDict[resolvedType]({ ...description });
};

const view = ({ description }) => {
    if (isEmpty(description)) return null;

    const descriptionElem = getDescriptionElem(description);

    return (
        <div className="genius-card__text genius-card__description">
            {descriptionElem}
        </div>
    );
};

export default view;
