import '@servicenow/now-label-value';
import isEmpty from 'lodash/isEmpty';
import { HORIZONTAL } from '../constants';

const view = ({ labelValuesInfo }) => {
    const { labelValues = [], align = HORIZONTAL } = labelValuesInfo;
    if (isEmpty(labelValues)) {
        return null;
    }

    return (
        <div className="genius-card__label-values">
            <now-label-value-stacked
                items={labelValues}
                size="md"
                align={align}
                truncated
            />
        </div>
    );
};

export default view;
