import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import mediaQueryBehavior from '@servicenow/behavior-media-query';
import view from './view';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';
import {
    HORIZONTAL,
    VERTICAL,
    HORIZONTAL_EQUAL,
    MOBILE_MEDIA_WIDTH,
} from './constants';

/**
 * Seismic component to build genius cards.
 *
 * ```jsx
 * <sn-search-genius-card-assist
 *     actions={[
 *         {
 *             assignmentId: '42fd3e5273511010c342d5fdbdf6a70e',
 *             name: 'activate_user',
 *             label: 'Activate',
 *         },
 *         {
 *             assignmentId: 'e3a173c073151010c342d5fdbdf6a7a2',
 *             name: 'call_number',
 *             label: 'Call Number',
 *         },
 *     ]}
 *     model={{
 *         name: 'Mac Marksberry',
 *         home_phone: '8581234567',
 *         location: 'Bockenheimer LandstraÃŸe 123, Frankfurt',
 *         department: 'Customer Support',
 *         sysId: 'fd826bf03710200044e0bfc8bcbe5d30',
 *         sys_id: 'fd826bf03710200044e0bfc8bcbe5d30',
 *         table: 'sys_user',
 *     }}
 *     identifier="Hyderabad, India"
 *     identifierCriticalText="4:30 pm"
 *     heading="Samina Arizmendi"
 *     marker={{
 *         type: 'avatar',
 *         value: '12245411112324da1.iix',
 *     }}
 *     description={{
 *         type: 'stacked',
 *         values: [
 *             'Manager, Marketing Systems',
 *             'Global Marketing Operations Department',
 *         ],
 *     }}
 * />
 *
 * ```
 *
 * @seismicElement sn-search-genius-card-assist
 * @summary Seismic component to build genius cards.
 */
createCustomElement('sn-search-genius-card-assist', {
    renderer: { type: snabbdom },
    view,
    initialState: {
        openSplitButtonDropdown: false,
    },
    properties: {
        /**
         * This property specifies the metadata for the actions to render in the genius card at the footer.
         * @type {array}
         */
        actions: {
            default: [],
            schema: {
                type: 'array',
            },
        },
        /**
         * This is an object that contains data associated to the card.
         * @type {object}
         */
        model: {
            default: {},
            schema: {
                type: 'object',
            },
        },
        /**
         * This property specifies the identifier text to render.
         * @type {string}
         */
        identifier: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies critical text to render next to the identifier.
         * This text is rendered in red.
         * @type {string}
         */
        identifierCriticalText: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * This property specifies the heading for the card.
         * @type {string}
         */
        heading: {
            default: '',
            schema: {
                type: 'string',
            },
        },
        /**
         * Specifies the marker for the genius card. Can render an icon, avatar, or image
         * @type ('image' | 'avatar' | 'icon')
         */
        marker: {
            default: {},
            schema: {
                type: 'object',
            },
        },

        /**
         * List the label values to render in the card in place of description.
         * @type {object}
         * @default { labelValues: [], align: 'horizontal' }
         */
        labelValuesInfo: {
            default: {
                labelValues: [],
                align: HORIZONTAL,
            },
            schema: {
                type: 'object',
                properties: {
                    labelValues: {
                        type: 'array',
                    },
                    align: {
                        type: 'string',
                        enum: [VERTICAL, HORIZONTAL, HORIZONTAL_EQUAL],
                    },
                },
            },
        },
        /**
         * Specifies the description text(s) for the genius card.
         * Can render up to two lines of description text or one HTML snippet.
         * When using the HTML type, only the first item of the values array will be rendered.
         * Use string value for a single description text.
         * If you pass in the object, it expects this format
         * ```jsx
         * {
         *     type: 'stacked' | 'html',
         *     values: []
         * }
         * ```
         * @type {string} | {object}
         */
        description: {
            default: '',
            schema: {
                oneOf: [
                    {
                        type: 'string',
                    },
                    {
                        type: 'object',
                    },
                ],
            },
        },
    },
    actionHandlers,
    styles,
    behaviors: [
        {
            behavior: rtlBehavior,
        },
        {
            behavior: mediaQueryBehavior,
            options: {
                mobileQuery: `(max-width: ${MOBILE_MEDIA_WIDTH})`,
            },
        },
    ],
});
